@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600&family=Roboto:ital,wght@0,400;0,500;0,700;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
    // font-style: italic;
    // text-transform: uppercase;
    transition: all .2s linear;
}
html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body{
    overflow-x: hidden;
}


navbar{
    display: flex;
    justify-content: space-between;
    background: linear-gradient(90deg, rgb(28, 27, 27)0%, rgb(26,23,23) 100%);
    height: 9vh;
    align-items: center;
    .logo {
        margin-left: 8rem;
        a{
            color: #fff;
            text-decoration: none;
            font-size: 2.4rem;
            font-weight: 700;
            i{
                font-size: 2.4rem;
                color: #fff;
                &:hover{
                    color: #fff;
                }
            }
        }
    }
    .icon{
        display: none;
        @media screen and (max-width: 968px) {
            display: block;
            color: #fff;
            font-size: 2.4rem;
            margin-right: 9rem;
            cursor: pointer;
        }
    }
    .nav-menu{
        display: flex;
        list-style-type: none;
        width: 70vw;
        justify-content: flex-end;
        margin-right: 8rem;
        padding-top: 1rem;
        @media screen and (max-width:968px) {
            flex-direction: column;
            position: absolute;
            background-color: #242424;
            top: 5.9rem;
            width: 100%;
            // height: 55rem;
            padding-bottom: 5rem;
            // padding-top: 3rem;
            text-align: center;
            left: -100%;
            transition: all .5s ease-in-out;
        }
        
        .nav-item{
            @media screen and (max-width: 968px) {
                padding: 3rem 0;
                // font-size: 4rem;
                // letter-spacing: .1rem;
                // font-weight: bold;
                &:hover{
                    background-color: #fff;
                    a{
                        color: #000;
                    }
                }
              }
        }
        .nav-item a{
            text-decoration: none;
            // background-color: red;
            color: #fff;
            font-size: 1.6rem;
            margin: 0 2rem;
            padding: 2.5rem 0;
            &:hover{
                border-bottom: .2rem solid #fff;
                margin-bottom: -2.5rem;           
            }
        }
        .nav-item-mobile{
            a{
                color: #fff;
                font-size: 1.7rem;
                padding: .8rem 2rem;
                text-decoration: none;
                border: .2rem solid #fff;
                margin-left: 1.1rem;
                border-radius: .3rem;
                // width: 40%;
                &:hover{
                    background-color: #fff;
                    color: #000;
                }
                @media screen and (max-width: 968px) {
                    // width: 80%;
                    padding: 1rem 20rem;
                }
                
            }
            @media screen and (max-width: 968px) {
                margin-top: 3rem;
                margin-left: -1rem;
                // border: none;
                // width: 30%;
                // padding: 0 10rem!important;
                padding-bottom: 25rem;
            }
        }
    }
    .nav-menu.active{
        @media screen and (max-width: 968px) {
            left: 0;
        transition: all .5s ease-in-out;
        }
    }
}

// Home section............................


video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}
.hero-container{
    // background: url(./components/images/img-home.jpg) center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 100rem rgba(0,0,0,0.2);
    object-fit: contain;
    h1{
        color: #fff;
        font-size: 10rem;
        margin-top: -10rem;
        @media screen and (max-width:1040px) {
            font-size: 7rem;
        }
        @media screen and (max-width:1040px) {
            font-size: 5rem;
        }
        @media screen and (max-width:768px) {
            margin-top: 10rem;
        }
        @media screen and (max-width:520px) {
            font-size: 4rem;
        }
        @media screen and (max-width:416px) {
            font-size: 3rem;
        }
    }
    p{
        margin-top: .8rem;
        color: #fff;
        font-size: 3.2rem;
        @media screen and (max-width:1040px) {
            font-size: 3rem;
        }
        @media screen and (max-width:520px) {
            font-size: 2.6rem;
        }
        @media screen and (max-width:416px) {
            font-size: 2.3rem;
        }
    }
    .hero-btns{
        margin-top: 2rem;
        a{
            color: #fff;
            text-decoration: none;
            margin: .8rem;
            font-size: 1.5rem;
            border: .2rem solid #fff;
            padding: 1rem 2rem;
            cursor: pointer;
            &:hover{
                background-color: #fff;
                color: #000;
                font-weight: bold;
            }
        }
        .btn-two{
            background-color: #fff;
            color: #000;
            font-weight: bold;
            cursor: pointer;
            &:hover{
                background-color: transparent;
                color: #fff;
            }
        }
        @media screen and (max-width: 768px) {
            display: flex;
            flex-direction: column;
            text-align: center;
        }
    }
}

// Cards Section


.cards{
    padding: 4rem;
    background-color: #fff;
    h1{
        text-align: center;
    }
    .card-container{
        display: flex;
        flex-flow: column;
        align-items: center;
        max-width: 100rem;
        width: 90%;
        margin: 0 auto;
    }
    .card-wrapper{
        position: relative;
        margin: 5rem 0 4.5rem;
    }
    .cards-items{
        margin-bottom: 2.4rem;
    }    
}
.cards-item{
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 1rem;
    .cards-item-link {
        display: flex;
        flex-flow: column;
        width: 100%;
        box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
        -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
        filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
        border-radius: 10px;
        overflow: hidden;
        text-decoration: none;
        .cards-item-pic {
            position: relative;
            width: 100%;
            padding-top: 67%;
            overflow: hidden;
            &::after{
                    content: attr(data-category);
                    position: absolute;
                    bottom: 0;
                    margin-left: 10px;
                    padding: 6px 8px;
                    max-width: calc((100%) - 60px);
                    font-size: 12px;
                    font-weight: 700;
                    color: #fff;
                    background-color: #1f98f4;
                    box-sizing: border-box;
            }
            .fade-img {
                animation-name: fade-img;
                animation-duration: 2s;
              }
              .cards-item-img {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                display: block;
                width: 100%;
                max-width: 100%;
                height: 100%;
                max-height: 100%;
                object-fit: cover;
                transition: all 0.2s linear;
                &:hover{    
                   transform: scale(1.1);
                }
              }
          }
      }
      .cards-item-info {
        padding: 20px 30px 30px;
        .cards-item-text {
            color: #252e48;
            font-size: 18px;
            line-height: 24px;
          }
      }
}

@media only screen and (min-width: 1200px) {
    .content-blog-container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards-items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards-item {
      margin-bottom: 2rem;
    }
}


