// Footer ....................




.footer-container{
    background-color: #242424;
    padding: 4rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .footer-subscription{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin-bottom: 2.4rem;
        padding: 2.4rem;
        color: #fff;
        .footer-subscription-heading{
            margin-bottom: 2.4rem;
            font-size: 2.4rem;
        }
        .footer-subscription-text{
            margin-bottom: 2.4rem;
            font-size: 2rem;
        }
        .footer-input{
            padding: .8rem 2rem;
            border-radius: .2rem;
            margin-right: 1rem;
            outline: none;
            border: none;
            font-size: 1.8rem;
            margin-bottom: 1.6rem;
            border: .1rem solid #fff;
            @media screen and (max-width: 820px) {
                width: 100%;
            }
        }
        .footer-input::placeholder{
            color: #b1b1b1;
        }
        a{
            color: #fff;
            font-size: 1.7rem;
            margin-bottom: .9rem;
            padding: .8rem 2rem;
            border: .2rem solid #fff;
            &:hover{
                background-color: #fff;
                color: #242424;
                font-weight: 700;
            }
        }
    }
    .footer-links{
        width: 100%;
        max-width: 100rem;
        display: flex;
        justify-content: center;
        @media screen and (max-width: 820px) {
            padding-top: 2rem;
        }
        .footer-link-wrapper{
            display: flex;
            @media screen and (max-width: 820px) {
                flex-direction: column;
            }
            .footer-link-items{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin: 1.6rem;
                text-align: left;
                width: 16rem;
                box-sizing: border-box;
                h2{
                    margin-bottom: 2rem;
                    color: #fff;
                }
                a{
                    color: #fff;
                    text-decoration: none;
                    margin-bottom: .8rem;
                    font-size: 1.5rem;
                    &:hover{
                        color: #e9e9e9;
                        transition: .3s ease-out;
                    }
                }
            }
        }
    }
    .social-media{
        max-width: 100rem;
        width: 100%;
        .social-media-wrap{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 90%;
            max-width: 100rem;
            margin: 4rem auto 0 auto;
            @media screen and (max-width: 820px) {
                flex-direction: column;
            }
            .social-icon-link{
                color: #fff;
                font-size: 2.4rem;
            }
            .social-icons{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 24rem;
            }
            .social-logo{
                color: #fff;
                justify-self: start;
                margin-left: 2rem;
                cursor: pointer;
                text-decoration: none;
                font-size: 2rem;
                display: flex;
                align-self: center;
                margin-bottom: 1.6rem;
            }
            .website-rights{
                color: #fff;
                margin-bottom: 1.6rem;
            }
        }
    }
}




